<template>
  <div class="basic-component">
    <div>
      <el-button type="text" icon="el-icon-refresh" @click="handleRefresh">刷新列表</el-button>
    </div>
    <div class="basic-component__list flex flex-wrap">
      <div
        v-for="item in list"
        :key="item.id"
        class="basic-component__item"
        @click="handleClick(item)"
      >
        <div class="basic-component__cover">
          <img :src="item.cover" width="100%" alt="">
        </div>
        <p class="mt5">{{ item.name }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from '@vue/composition-api'
import { addNode, addBeforeValidate } from '@/assets/node'
import { project } from '@/assets/project'
import { setTabName, tabName } from '@/assets/tab'
import { http } from '@/api'

export default {
  components: {
  },
  setup () {
    const list = ref([])
    http.get('component/list', { type: 'basic' }).then(res => {
      list.value = res.data
    })
    const handleRefresh = () => {
      http.post('component/update', { type: 'basic' }).then(res => {
        list.value = res.data
      })
    }
    const handleClick = (item) => {
      if (addBeforeValidate()) {
        addNode({ ...item, nodeType: 1 << 0 })
        setTabName([tabName.nodeTree, '', tabName.nodeProperty])
        project.componentDownload.push(item)
      }
    }
    return {
      list,
      handleClick,
      handleRefresh
    }
  }
}
</script>

<style lang="less">
.basic-component {
  &__list {
    border-top: 1px #e2e2e2 solid;
    border-left: 1px #e2e2e2 solid;
  }
  &__item {
    width: 89px;
    padding: 10px 0;
    border-right: 1px #e2e2e2 solid;
    border-bottom: 1px #e2e2e2 solid;
    color: #696969;
    text-align: center;
    cursor: pointer;
  }
  &__cover {
    width: 30px;
    height: 30px;
    background-color: #f2f2f2;
    margin: 0 auto;
  }
}
</style>
